import { Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { AllModulesService } from "src/app/all-modules/all-modules.service";
import { FormGroup, FormBuilder, Validators,FormControl } from "@angular/forms";
import { ToastrService } from "ngx-toastr";
import { DatePipe } from "@angular/common";
import { Subject } from "rxjs";
import { DataTableDirective } from "angular-datatables";
import { HrCrEmp } from "../../../model/HrCrEmp";
import { EmployeeService } from "../../../services/employee.service";
import { NgxSpinnerService } from "ngx-spinner";
import { environment } from "src/environments/environment";
import { Designations } from "../../../model/Designations";

declare const $: any;
@Component({
  selector: "app-employee-list",
  templateUrl: "./employee-list.component.html",
  styleUrls: ["./employee-list.component.css"],
})
export class EmployeeListComponent implements OnInit, OnDestroy {


  
  public baseUrl = environment.baseUrl;

  // designation
  public designations: Designations[];
  public empList:HrCrEmp[];
  public lstEmployee: any;
  public url: any = "employeelist";
  public tempId: any;
  public editId: any;

  public addEmployeeForm: FormGroup;
  public editEmployeeForm: FormGroup;

  public pipe = new DatePipe("en-US");
  public rows = [];
  public srch = [];
  public statusValue;
  public dtTrigger: Subject<any> = new Subject();
  public DateJoin;t


   //pagination config
   pageNum = 1;
   pageSize = 10;
   pageSizes = [3, 5, 10, 25, 50, 100, 200, 500, 1000];
   totalItem = 50;
   pngDiplayLastSeq = 10;
   pngConfig: any;
 
   public myGroup: FormGroup;

  constructor(
    private srvModuleService: AllModulesService,
    private toastr: ToastrService,
    private formBuilder: FormBuilder,
    private employeeService: EmployeeService,
    private spinnerService: NgxSpinnerService,
  ) { }

  ngOnInit() {

    this.myGroup = new FormGroup({
      pageSize: new FormControl()
    });
    this.myGroup.get('pageSize').setValue(this.pageSize);

    this.getDesignations();


    // for floating label

    $(".floating")
      .on("focus blur", function (e) {
        $(this)
          .parents(".form-focus")
          .toggleClass("focused", e.type === "focus" || this.value.length > 0);
      })
      .trigger("blur");


    this.loadAllEmployee();
  
  }



  ngAfterViewInit(): void {
    setTimeout(() => {
      this.dtTrigger.next();
    }, 1000);
  }
  // manually rendering Data table

  rerender(): void {
   
  }

  getDesignations() {
    this.employeeService.getDesignations().subscribe(
      (data: any) => {
        this.designations = data;
      },
      (error) => {
        this.toastr.error(error.error.message);
      }
    );
  }





  // to know the date picker changes
  from(data) {
    this.DateJoin = this.pipe.transform(data, "dd-MM-yyyy");
  }


  // delete employee data api call
  deleteEmployee() {

    this.toastr.success("Employee deleted sucessfully..!", "Success");
  }


  // MyCustom Code 
  loadAllEmployee() {

    let apiURL = this.baseUrl + "/hrCrEmp/empListData";

    let queryParams: any = {};
    
    const params = this.getUserQueryParams(this.pageNum, this.pageSize);
    queryParams = params;

    this.spinnerService.show();
    this.employeeService.sendGetRequest(apiURL, queryParams).subscribe(
      (response: any) => {
        this.empList = response.objectList;
        this.totalItem = response.totalItems;
        this.setDisplayLastSequence();
        this.spinnerService.hide();  
      },
      (error) => {
        console.log(error)
      } 
    );
  }


  getUserQueryParams(page: number, pageSize: number): any {

    let params: any = {};

    if (page) {
      params[`pageNum`] = page - 0;
    }
    if (pageSize) {
      params[`pageSize`] = pageSize;
    }

    return params;
    
  }
 

  setDisplayLastSequence(){
    this.pngDiplayLastSeq = (((this.pageNum - 1 ) * this.pageSize) + this.pageSize);
    if(this.empList.length < this.pageSize){
      this.pngDiplayLastSeq = (((this.pageNum - 1 ) * this.pageSize) + this.pageSize);
    }
    if(this.totalItem < this.pngDiplayLastSeq){
      this.pngDiplayLastSeq = this.totalItem;
    }
  }
  handlePageChange(event: number){
    this.pageNum = event;
    this.loadAllEmployee();
  } 
  handlePageSizeChange(event: any): void {
    this.pageSize = event.target.value;
    this.pageNum = 1;
    this.loadAllEmployee();
  }

  //End MyCustom


  //search by Id
  searchId(val) {
    let apiURL = this.baseUrl + "/hrCrEmp/empList2";

    let queryParams: any = {};

    queryParams.loginCode=val;

    this.spinnerService.show();
    this.employeeService.sendGetRequest(apiURL, queryParams).subscribe(
      (response: any) => {
        this.spinnerService.hide();  
        this.empList = response.objectList;
        this.totalItem = response.totalItems;
        this.setDisplayLastSequence();
        
      },
      (error) => {
        console.log(error)
      } 
      
    );
  }

  //search by name
  searchName(val) {
    let apiURL = this.baseUrl + "/hrCrEmp/empList2";

    let queryParams: any = {};

    queryParams.displayName=val;

    this.spinnerService.show();
    this.employeeService.sendGetRequest(apiURL, queryParams).subscribe(
      (response: any) => {
        this.empList = response.objectList;
        this.totalItem = response.totalItems;
        this.setDisplayLastSequence();
        this.spinnerService.hide();  
      },
      (error) => {
        console.log(error)
      } 
    );
  }

  //search by purchase
  searchByDesignation(val) {
    let apiURL = this.baseUrl + "/hrCrEmp/empList2";

    let queryParams: any = {};

    queryParams.designationId=val;

    this.spinnerService.show();
    this.employeeService.sendGetRequest(apiURL, queryParams).subscribe(
      (response: any) => {
        this.empList = response.objectList;
        this.totalItem = response.totalItems;
        this.setDisplayLastSequence();
        this.spinnerService.hide();  
      },
      (error) => {
        console.log(error)
      } 
    );
  }

  //getting the status value
  getStatus(data) {
    this.statusValue = data;
  }
  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }
}
